/* Preloader */
.cube-loader {
  align-items: center;
  display: flex;
  height: 100%;
  width: 100%;
  position: absolute;
  background: #F9F9F9;
  z-index: 9999; }
  .cube-loader .caption {
    margin: 0 auto; }
  .cube-loader .cube-cont {
    width: 40px;
    height: 40px;
    margin: 0 auto;
    position: relative;
    transform: rotateZ(45deg); }
    .cube-loader .cube-cont .cube {
      position: relative;
      transform: rotateZ(45deg);
