      animation: cube-loader 2.76s infinite linear both;
      transform-origin: 100% 100%; }
    .cube-loader .cube-cont .loader-2 {
      transform: scale(1.1) rotateZ(90deg); }
    .cube-loader .cube-cont .loader-3 {
      transform: scale(1.1) rotateZ(180deg); }
    .cube-loader .cube-cont .loader-4 {
      transform: scale(1.1) rotateZ(270deg); }
    .cube-loader .cube-cont .loader-2:before {
      animation-delay: 0.35s; }
    .cube-loader .cube-cont .loader-3:before {
      animation-delay: 0.69s; }
    .cube-loader .cube-cont .loader-4:before {
